/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { InvitationsComponent } from '../Components/invitations/invitations/invitations.component';
import { InvitationProfileComponent } from '../Components/invitations/invitation-profile/invitation-profile.component';
import { InvitationProjectsComponent } from '../Components/invitations/invitation-projects/invitation-projects.component';

/* Librairies */

const routes: Routes = [
	{ path: '', component: InvitationsComponent,
		children: [
			{ path: '', redirectTo: ':invite_id', pathMatch: 'full' },
			{ path: ':invite_id', component: InvitationProfileComponent },
			{ path: 'welcome/:network/:token', component: InvitationProfileComponent },
			{ path: 'projects/:project_token', component: InvitationProjectsComponent }
		]
	},
];

export const INVITE_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



