import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
/* Services */
import { SharedService } from '../../../Services/shared.service';
import { AccountService } from '../../../Services/account.service';
import { AuthenticationService } from '../../../Services/Authentication/auth-index';

@Component({
	selector: 'app-invitation-projects',
	templateUrl: '../invitation-profile/invitation-profile.component.html',
	styleUrls: [
		'../invitation-profile/invitation-profile.component.scss',
		'../../../../public/styles/login-signup-modal.scss'
	],
	providers: [AccountService, AuthenticationService]

})
export class InvitationProjectsComponent implements OnInit {

	form 		: any = {}
	fn_err 		: boolean = false
	ln_err 		: boolean = false
	email_err 	: boolean = false
	pw_err 		: boolean = false
	visible 	: boolean = true
	invite1 	: boolean = false
	invite2 	: boolean = false
	invite3 	: boolean = false
	profile 	: Object
	invite_id 	: string
	network 	: string

	validateForm(form) {
		this.fn_err = this.ln_err = this.email_err = this.pw_err = true

		if (form.first_name)
			this.fn_err = false
		if (form.last_name)
			this.ln_err = false
		if (form.email) {
			const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			this.email_err = !reg.test(form.email)
		}
		if (form.password)
			this.pw_err = form.password.length >= 8 ? false : true
		return (this.fn_err && this.ln_err && this.email_err && this.pw_err)
	}

  	constructor(private AccountService: AccountService,
				private AuthenticationService: AuthenticationService,
				private route: ActivatedRoute,
				private router: Router) {
	}

	ngOnInit() {
		this.initParams();
	}

	initParams() {
		this.route.params.subscribe(params => {
			if (params['project_token']) {
				this.initCookie(params['project_token']);
			}
		});
	}

	initCookie(token) {
		localStorage.setItem('project', token);
	}

	signUp() {
		if (!this.validateForm(this.form)) {
			let body = { account: this.form };
			let login = { email: this.form.email, password: this.form.password };

			this.AccountService.createProfile(body).subscribe( res => {
				if (res.success) {
					this.AuthenticationService.localLogIn(login).subscribe( res => {
						this.router.navigate(['/signup'], { queryParams: { id: true } });
			        });
				}
			});
		}
	}

	getMargin() {
		return '45px'
	}
}



