import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

/* Services */
import { SharedService } from '../../../Services/shared.service';
import { AccountService } from '../../../Services/account.service';
import { AuthenticationService } from '../../../Services/Authentication/auth-index';
import { InvitationService } from '../../../Services/Invitation/invitation.service';
import { NetworksService } from '../../../Services/Networks/networks.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { TrackingService } from '../../../Services/Trackings/tracking.service';

/* Animations */

@Component({
	selector: 'app-invitation-profile',
	templateUrl: './invitation-profile.component.html',
	styleUrls: [
		'./invitation-profile.component.scss',
		'../../../../public/styles/login-signup-modal.scss'
	],
	providers: [AccountService, AuthenticationService, InvitationService, NetworksService]
})
export class InvitationProfileComponent implements OnInit {

	form 		: any = {}
	fn_err 		: boolean = false
	ln_err 		: boolean = false
	email_err 	: boolean = false
	pw_err 		: boolean = false
	visible 	: boolean = true
	invite1 	: boolean = false
	invite2 	: boolean = false
	invite3 	: boolean = false
	profile 	: Object
	invite_id 	: string
	network 	: string

	hours 		: number
	minutes 	: number
	seconds 	: number
	myLink 		: boolean = false
	expired 	: boolean = false


	validateForm(form) {
		this.fn_err = this.ln_err = this.email_err = this.pw_err = true

		if (form.first_name)
			this.fn_err = false
		if (form.last_name)
			this.ln_err = false
		if (form.email) {
			const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			this.email_err = !reg.test(form.email)
		}
		if (form.password)
			this.pw_err = form.password.length >= 8 ? false : true
		return (this.fn_err && this.ln_err && this.email_err && this.pw_err)
	}

  constructor(private SharedService: SharedService,
              private AccountService: AccountService,
              private AuthenticationService: AuthenticationService,
              private route: ActivatedRoute,
              private router: Router,
              private InvitationService: InvitationService,
              private NetworksService: NetworksService,
              private TokenService: TokenService,
              private TrackingService: TrackingService) {
		this.initParams();
	}

	ngOnInit() {
		this.form = {
			first_name	: '',
			last_name 	: '',
			email 		: '',
			password 	: ''
		}
	}

	trackViewPage(page_id: number) {
		const token = this.TokenService.getToken();

		this.TrackingService.viewActivities({}, token, page_id).subscribe(r => {});
	}

	initCookie(name) {
		localStorage.setItem('network', name);
	}

	initParams() {
		this.route.params.subscribe(params => {
			if (params['invite_id'] !== 's5fZnFqodHSDS8SOPHfWZxUf2ssFbEnP') {
				this.invite1 = true
				this.invite_id = params['invite_id'];
				this.getUserByInviteId(this.invite_id);
				this.trackViewPage(2);
			} else if (params['invite_id'] === 's5fZnFqodHSDS8SOPHfWZxUf2ssFbEnP') {
				this.invite3 = true;
				this.setDeadlineTime();
				this.trackViewPage(1);
			} else {
				this.network = params['network'];
				this.getNetworkInfo(params['token']);
				this.trackViewPage(3);
			}
		});
	}

	setDeadlineTime() {
		const token = this.TokenService.getToken();
		const d 	= new Date();

		if (token && this.profile && token.user.id === this.profile['uid'])
			this.myLink = true;
		if (!this.myLink) {
			if (!localStorage.getItem('invite-counter')) {
				localStorage.setItem('invite-counter', d.getTime().toString())
				this.counterFromTimestamp(d.getTime());
			} else {
				this.counterFromTimestamp(parseInt(localStorage.getItem('invite-counter')));
			}
		}
	}

	counterFromTimestamp(timestamp) {
		const d 		= new Date();
		let timeLimit 	= 1000 * 60 * 60 * 13 - 830046;
		let timeSpend 	= d.getTime() - timestamp;
		let timeRemain 	= (timeLimit - timeSpend) / 1000

		if (timeRemain > 0) {
			setInterval( () => { // execute code each second

			    timeRemain--; // decrement timestamp with one second each second

		    	this.hours   = this.component(timeRemain,      60 * 60) % 24, // hours
		        this.minutes = this.component(timeRemain,           60) % 60, // minutes
		        this.seconds = this.component(timeRemain,            1) % 60; // seconds

		        if (timeRemain < 0)
		        	this.expired = true;

			}, 1000); // interval each second = 1000 ms
		} else
			this.expired = true;
	}

	component(x, v) {
	    return Math.floor(x / v);
	}

	getNetworkInfo(token) {
		this.NetworksService.getNetworkInvitation(token).subscribe( res => {
			if (res.informations[0]) {
				this.invite2 = true
        		this.initCookie(res.informations[0]['name']);
			} else
				this.router.navigate(['/']);

		});
	}

	getUserByInviteId(id) {
		this.InvitationService.getUserByInviteId(id).subscribe( res => {
			this.profile = res.informations[0];
			this.setDeadlineTime();
		});
	}

	switchL() {
		setTimeout( () => this.SharedService.setSwitch('Signup'), 300);
		this.visible = false
	}

	closeSignUpModal() {
		setTimeout( () => this.SharedService.setSignUpStatus(false), 300);
		this.visible = false
	}

	signUp() {
		if (!this.validateForm(this.form)) {
			let body = { account: this.form };
			let login = { email: this.form.email, password: this.form.password };

			this.AccountService.createProfile(body).subscribe( res => {
				if (res.success) {
					this.closeSignUpModal();
					this.AuthenticationService.localLogIn(login).subscribe( res => {
						this.router.navigate(['/signup'], { queryParams: { id: true } });
			        });
				}
			});
		}
	}

	registerInvitation() {
		this.InvitationService.registerInvitation(this.invite_id, { email: 'hello@witty.com' }).subscribe( res => {
		});
	}

	getMargin() {
		return this.invite2 ? '35px' : 0
	}
}



